import React from 'react'
import automationTool from '../assets/automation-tool.png'
// import ticketRunner from '../assets/ticket-runner.png'
import ticketRunnerTwo from '../assets/ticket-runner-2.png'

function medscapeExtra(props) {
  return (
    <div className="extra selectim-extra">
      <div className="project-text">
        <figure style={{'marginBottom': '50px'}}>
          <img src={automationTool} alt="Sample of Automation Tool's UI" />
          <figcaption>
            The Automation Tool 
          </figcaption>
        </figure>
        <p>
          My first project, the "Automation Tool", was a program I inherited from a prior developer
          and was responsible for maintaining - a tool used by our contractors for converting Word documents 
          and Exel spreadsheets into XML ready for upload to the CMS (using exhaustive lists of regular expressions).
          The version I was given was only partially complete: It had major bugs and existed only as a command-line 
          interface. I refactored and updated the core code to work with most of Medscape's products. I also took 
          the initiative to convert the tool into a single-page website, a decision which vastly increased the
          adoption of the tool by making it more user-friendly and maintainable. Quality assurance reported a 40%
          reduction in production errors when the new version was adopted, and contractors can save up to 1 to 2
          hours per product processed with the tool.
        </p>
        <figure style={{'margin': '50px 0px'}}>
          <img src={ticketRunnerTwo} alt="Sample of Ticket Runner's UI" />
          <figcaption>
            Ticket Runner
          </figcaption>
        </figure>
        <p>
          For my second project, "Ticket Runner", I had the opportunity to start from scratch on an ambitious attempt
          to re-imagine how content is delivered for our division of the organization. Ticket Runner (TR) replaces
          an antiquated "production ticket" Word document with a fully-featured webform. The project requirements
          included a variety of complex features: A versioning system, track-changes functionality similar to MSWord,
          support for comments with @mentions and push notifications, and the ability to generate various file formats
          from the JSON data of the form, including PDFs, XML, html pages, and even images and QR codes.
        </p>
        <p>
          Ticket Runner also needed to accomodate a variety of users with different use-cases, from those who needed
          different methods of previewing the content and reviewing submissions, to users who required complex 
          word-processing, to management with the ability to add/remove users, delegate permissions, and lock out 
          different fields.
        </p>
        <p>
          Ticket Runner was created in a team of between 2-4 developers, using Vue.js, Quasar, Firebase, Google 
          Authentication and APIs, and many external JS libraries and APIs, including a custom version of CKEditor.   
        </p>
      </div>
    </div>
  )
}

export default medscapeExtra
