import React from 'react';

//Images
import selectimImg from '../assets/Selectim_Mockup_Home.jpg'
import ofHomeImg from '../assets/OfHome_Donate.png'
import paintingImg from '../assets/paintings.png'
import medscapeSite from '../assets/medscape-site.png'

//Components
import MedscapeExtra from './medscapeExtra'
import OfHomeExtra from './OfHomeExtra'
import ProjectLinks from './ProjectLinks'
import SelectimExtra from './SelectimExtra'
import PersonalProjects from './PersonalProjects'
import RelatedLinks from './RelatedLinks'

const projects = {
  ofHome: {
    text: `Of Home, Family & Future is a nonprofit organization "formed to offer a different kind of assistance to domestic violence survivors and college-bound foster care youth." Their website, last updated in 2005, was badly in need of a redesign. Working from the specifications given by the organization's board of directors, I personally designed and implemented a completely new version of the site. Technologies used for this project inclued Wordpress (including the design in PHP of a custom Wordpress theme & widget), HTML5/CSS, and Javascript + jQuery.`,
    title: 'Of Home, Family & Future',
    skills: {
      frontend: ['Javascript', 'Responsive HTML5', 'CSS'],
      backend: ['PHP', 'Wordpress']
    },
    image: ofHomeImg,
    imageAlt: 'Of Home, Family & Future Homepage',
    extraHTML: <OfHomeExtra />,
    links: <RelatedLinks site="http://www.ofhome.org" github="https://github.com/dylonion/ofHome" />
  },
  selectim: {
    text: `Each morning, agents in the fiercly competitive Parisian real estate market scramble to be the first to locate
          undiscovered new properties, and to fulfill the demands of their clients: A two bedroom walk-up apartment, no
          higher than the second floor, in so-and-so neighborhood, for x amount of euros, etc. In the internet age, this
          necessitates the ability to very quickly launch a high volume of very specific search queries that span a number
          of different listing websites - personals sites; the French equivalents of Craigslist.
          The goal of Selectim, founded in 2005, was to provide a place that aggregated together the real estate listings
          from a variety of these sites, and give agents tools for searching with specificity that didn't yet exist in France.`,
    title: 'Selectim',
    skills: {
      frontend: ['Javascript', 'jQuery'],
      backend: ['PHP','Perl','Bash','Network Administration', 'mySQL']
    },
    image: selectimImg,
    imageAlt: 'Sample image of Selectim\'s UI in action',
    extraHTML: <SelectimExtra />,
    links: <RelatedLinks github="https://github.com/dylonion/selectim" />
  },
  thisSite: {
    text: `Although I first started coding for myself in PHP, I fell in love with the Javascript and Node stack
    when I attended an immersive coding bootcamp at General Assembly. Over the course of three months,
    I worked on my own and in Agile teams of 2-3 other students on several demanding projects,
    utilizing JS, Ruby, and modern frameworks, libaries, and programming techniques. Some of these projects
    I have continued to refine after graduating. `,
    title: 'General Assembly',
    skills: {
      frontend: ['JS6', 'React', 'Responsive HTML5', 'CSS','Socket.io'],
      backend: ['Node.js','postgreSQL','Express','Passport(Authentication)','Socket.io', 'Git','Bash','Ruby/Rails']
    },
    image: '',
    imageAlt: '',
    extraHTML: <PersonalProjects />
  },
  valerie: {
    text: `In establishing myself as a freelance web designer and developer, I found a niche designing custom
    personal websites for visual artists. One of these artists, Valerie Mendelson, was interested in a tool for indexing
    and archiving her extensive collection of paintings, as well as the ability to edit her own site through Wordpress.
    I am currently working to develop for her a custom API for uploading images and editing their jpeg file metadata,
    as a way of preserving information such as a painting's title and description in a more permanent fashion than a
    simple entry into a Wordpress database. The tools I used for this project include Wordpress, PHP, front-end javascript,
    and HTML/CSS.`,
    title: 'Freelance Projects',
    skills: {
      frontend: ['Responsive HTML5', 'CSS', 'Javascript'],
      backend: ['PHP', 'Wordpress', 'Bash']
    },
    image: paintingImg,
    imageAlt: 'A sample gallery page from valeriemendelson.net',
    extraHTML: '',
    links: <RelatedLinks site="https://valeriemendelson.net/wordpress" />
  },
  medscape: {
    text: `Medscape is the internet's #1 source of medical news and continuing education for health care professionals.
    Each month, thousands of articles are published by expert physicians, researchers, and educators. Working for the
    Online Production department in Medscape Education, our team was responsible for getting the content produced by
    editorial online as seamlessly as possible. As a developer, I created and maintained custom productivity software 
    to facilitate and automate the translation of spec documents into our final product. Working primarily with the Vue
    Javascript framework (extended with Quasar) and Google's Firebase on the back-end, my work at Medscape focused on two
    major projects:`,
    title: 'WebMD/Medscape',
    skills: {
      frontend: ['Vue.js', 'Quasar',  'Jest', 'CKEditor 5', 'JS6', 'Responsive HTML5', 'CSS'],
      backend: ['Firebase Realtime Database', 'G-Suite APIs', 'Headless Chrome', 'Regular Expressions', 'Node.js']
    },
    image: medscapeSite,
    imageAlt: '',
    extraHTML: <MedscapeExtra />
  }
}
function ProjectWindow(props) {
  return (
    <div className="project-window">
      <div className="title">
        {projects[props.project].title}
      </div>
      <div className={projects[props.project].image.length > 0 ? 'project-img' : 'project-img nodisplay'}>
        <img alt={projects[props.project].imageAlt} src={projects[props.project].image.length > 0 ? projects[props.project].image : '' } />
      </div>
      <div className="project-text">
        {projects[props.project].text}
      </div>
      <div className={projects[props.project].extraHTML ? 'extra-content' : 'extra-content nodisplay'}>
        {projects[props.project].extraHTML}
      </div>
      <div>{projects[props.project].links}</div>
      <div className="skills">
        <div className="front-end skill">
          <div className="skill-icon"><i className="fas fa-file-code"></i></div>
          <div className="bldr">Front End Skills</div>
          <ul>
            {
              projects[props.project].skills.frontend.map((item,index) => {
                return (
                  <li key={index}><i className="far fa-check-circle"></i>{item}</li>
                )
              })
            }
          </ul>
        </div>
        <div className={projects[props.project].skills.backend.length < 1 ? 'back-end skill nodisplay' : 'back-end skill'}>
          <div className="skill-icon"><i className="fas fa-code-branch"></i></div>
          <div className="bldr">Back End Skills</div>
              <ul>
                {
                  projects[props.project].skills.backend.map((item,index) => {
                    return (
                      <li key={index}><i className="far fa-check-circle"></i>{item}</li>
                    )
                  })
                }
              </ul>
          </div>
        </div>
        <ProjectLinks selected={props.project} />
      </div>
  )
}

export default ProjectWindow
